import React, { useState, useEffect } from "react";
import Container from "@mui/material/Container";
import CloudIcon from "@mui/icons-material/Cloud";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import axios from "axios";
import moment from "moment";
import { useTranslation } from "react-i18next";
import "moment/min/locales";
moment.locale("ar");

function Wether() {
  //  Translation
  const { t, i18n } = useTranslation();

  //  Date and Time
  const dataAndTime = moment().format(" (hh:mm) , Do - MMMM - YYYY");

  // All state
  const [weather, setWeather] = useState({
    temp: 0,
    tempMax: 0,
    tempMin: 0,
    description: "",
    icon: null,
    name: "",
  });

  // State for the city search input
  const API_KEY = "e84d6ee35b079eaa54182df886881861";
  const [city, setCity] = useState("");
  //translation
  const [local, setLocal] = useState("en");
  // Event button onClick for language change
  function handleLanguage() {
    if (local === "en") {
      setLocal("ar");
      i18n.changeLanguage("ar");
      moment.locale("ar");
    } else {
      setLocal("en");
      i18n.changeLanguage("en");
      moment.locale("en");
    }
  }

  // Fetch weather data based on city input
  function fetchWeather() {
    if (city) {
      const weatherUrl = `https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=${API_KEY}&lang=${local}`;

      // دى عشان اعرف اعمل clear للدله
      const source = axios.CancelToken.source();

      axios
        .get(weatherUrl, {
          cancelToken: source.token,
        })
        .then((response) => {
          console.log("response");
          console.log(response.data);
          const responsetemp = Math.round(response.data.main.temp - 273.15);
          const responsetempMax = Math.round(
            response.data.main.temp_max - 273.15
          );
          const responsetempMin = Math.round(
            response.data.main.temp_min - 273.15
          );
          const descrip = response.data.weather[0].description;
          const iconresponse = response.data.weather[0].icon;
          const cityName = response.data.name;

          setWeather({
            temp: responsetemp,
            tempMax: responsetempMax,
            tempMin: responsetempMin,
            description: descrip,
            icon: `https://openweathermap.org/img/wn/${iconresponse}@2x.png`,
            name: cityName,
          });
        })
        .catch((error) => {
          if (axios.isCancel(error)) {
            console.log("no error");
          } else {
            console.error("Error:", error);
          }
        });

      // Cleanup function
      return () => {
        source.cancel("Request cancelled.");
      };
    }
  }

  useEffect(() => {
    i18n.changeLanguage(local);
  }, [local]);

  // Call the cleanup function in useEffect to avoid memory leaks
  useEffect(() => {
    return () => {
      // Ensure proper cleanup
    };
  }, []);

  return (
    <div className="App">
      <Container maxWidth="sm">
        {/* Start Card */}
        <div className="card">
          <form
            onSubmit={(e) => {
              e.preventDefault();
              fetchWeather();
            }}
          >
            <input
              type="text"
              placeholder="Enter city or governorate in Egypt"
              value={city}
              onChange={(e) => setCity(e.target.value)}
              style={{ width: "95%", padding: "10px", marginBottom: "10px" }}
            />
            <Button
              variant="contained"
              style={{ background: "#0b2f9f" }}
              onClick={fetchWeather}
            >
              {/* {t("Search")} */}
              {local === "en" ? "Search" : "بحث"}
            </Button>
          </form>
          {/* Weather Content */}
          <div className="contaner">
            <div className="header">
              <h1>{t(weather.name)}</h1>
              <h4>{dataAndTime}</h4>
            </div>
            <hr />
            <div className="body">
              <div className="left">
                <CloudIcon style={{ fontSize: "200px" }}></CloudIcon>
              </div>
              <div className="right" style={{ textAlign: "right" }}>
                <div className="top">
                  {weather.icon ? (
                    <img src={weather.icon} alt="weather icon" />
                  ) : null}
                  <Typography variant="h1">{weather.temp}</Typography>
                </div>
                <div className="center">
                  <h5>{t(weather.description)}</h5>
                </div>
                <div className="bottom">
                  <h4>
                    {t("Min")} : {weather.tempMin}
                  </h4>
                  <h4>
                    {t("Max")} : {weather.tempMax}
                  </h4>
                </div>
              </div>
            </div>
          </div>

          {/* Language Switch Button */}
          <div className="zraar">
            <Button color="white" href="" onClick={handleLanguage}>
              {local === "en" ? "Arabic" : "انجليزى"}
            </Button>
          </div>
        </div>
        {/* End Card */}
      </Container>
      <div
        style={{ textAlign: "center", padding: "10px", background: "white" }}
      >
        made with <span style={{ color: "#0b2f9f" }}>Mohamed Omar</span>
      </div>
    </div>
  );
}

export default Wether;
